import React,{useState, useEffect} from 'react'
import "./style.css"
import axios from 'axios'
import { Link, useNavigate, useLocation} from "react-router-dom"
import {Appbar} from './common'
import ReactFileReader from "react-file-reader";

import Checkbox from '@mui/material/Checkbox';
import BookmarkBorderIcon from '@mui/icons-material/BookmarkBorder';
import BookmarkIcon from '@mui/icons-material/Bookmark';

import TextField from '@mui/material/TextField';
import {getProducts} from './ProductPanel';

import { loadingStart,dialogMsg, loadingStop, dialogError, dialogSuccess, dialogClose} from '../dialog/dialog'



const defalut_product_icon = "https://ik.imagekit.io/abhilasbiswas/default_product_icon.png?updatedAt=1678945307107"
const add_img = "https://ik.imagekit.io/abhilasbiswas/add_img.png"

const server = process.env.REACT_APP_SERVER;


function ProductSelect({product,update,index, selected}) {
    const [is_checked, setCheck] = useState(selected);
    return (
      <div className="product-select-card" onClick={()=>{
        setCheck(!is_checked);
        update(index, !is_checked);
      }}>
        
          <div className="icon">
            <img src={product.feature_images[0]} alt="product" />
          </div>
  
          <div className="info">
              <p className='title'>{product.title}</p>
              <p className='price'>{product.price}</p>
          </div>
          <div className="checkbox-select">
          <Checkbox 
          checked={is_checked}
          icon={<BookmarkBorderIcon />}
          checkedIcon={<BookmarkIcon />}/>
          </div>
      </div>
    )
  }


  function newCollection(collection, image, products, success){
    loadingStart();
    const link = server + "admin/collection/create";
    const data = new FormData();
    collection.products = products;
    data.append("token", localStorage.getItem("admin_token"))
    // console.log(collection)
    data.append("collection",JSON.stringify(collection));
    data.append("image",image)

    axios.post(link, data)
    .then((res)=>{
      loadingStop()
      if (res.data!=="-1"){
        success(res.data);
        dialogSuccess("Collection added successfully");
      }else{
        dialogError("Collection failed to add");
      }
    });
  }



  function updateCollection(collection, image, products){
    loadingStart();
    const link = server + "admin/collection/update";
    const data = new FormData();
    collection.products = products;
    data.append("token", localStorage.getItem("admin_token"))
    console.log(collection)
    data.append("collection",JSON.stringify(collection));
    
    data.append("image",image)
    
    axios.post(link, data)
    .then((res)=>{
      loadingStop();
      if (res.data==="SUCCESS"){
        dialogSuccess("Updated successfully")
      }else{
        dialogError("Update Failed")
      }
    });
  }




export default function CollectionUpdateView(props) {

    const state = useLocation().state;

    let model = {
        id: -1,
        title: "",
        tags: [],
        description: "",
        products: [],
        feature_image: ""
    };

    const [new_collection, setNewFlag] = useState(true);

    let preview = null;


    if (new_collection)
    if (state){
      if (state.model){
        model=  state.model;
        setNewFlag(false)

        preview = model.feature_image;
      }
    }


    const [collection,setCollection] = useState(model);


    const [image, setImage] = React.useState(collection.feature_image);
    const [image_preview, setPreview] = React.useState(preview);
    
    const [selected_list, setSelected] = useState(collection.products);
    
    
    const [products, setProducts] = useState([]);

    
    useEffect(()=>{
      if (products.length>0)
      return;
          getProducts(
            (res)=>
            {
              setProducts(res);
            })
      }
    );

    function updateSelectedProduct(index,flag){
      let new_list;
      if (flag){
        new_list = [...selected_list]
        new_list.push(products[index].id);
      }
      else{
        new_list = selected_list.filter(function(item) {
            return item !== products[index].id
        })
      }
      setSelected(new_list)
      // console.log(new_list)
    }
    
    function updateData(field){
        setCollection({
            id: collection.id,
            title: field.title?field.title:collection.title,
            feature_image: collection.feature_image,
            tags: field.tags?field.tags:collection.tags,
            description: field.description?
                         field.description :collection.description,
            products: []
        })
    }

    function addedSuccess(new_col){
      setCollection(new_col)
      setNewFlag(false)
    }

      return (
        <div className="adminpanel">

        <Appbar 
        title={new_collection?"New Collection":"Update Collection"} 
        back 
        nosearch  
        action={new_collection?
          ()=>
          newCollection(collection, image, selected_list, addedSuccess):
          ()=>
          updateCollection(collection, image, selected_list)}/>

        <div className="product-update-view">

          
          <ReactFileReader
            fileTypes={[".png", ".jpg"]}
            base64={true}
            handleFiles={(file)=>{
            setImage(file.fileList[0])
            setPreview(file.base64)
            }}>
                <img 
                id="collection_update_banner_preview" 
                src={image_preview?image_preview:add_img} 
                alt="img" />
            </ReactFileReader>


          <div className="container">
          <TextField 
          value={collection.title} 
          onChange={
            (event)=> 
            updateData({title: event.target.value})
          } 
          className='input-text' 
          id="outlined-basic" 
          label="Title" 
          variant="outlined"
          inputProps={{style:{width:"300px"}}} />
          </div>
          
          <div className="container">
          <TextField 
          value={collection.tags}  
          onChange={(event)=> 
            updateData({tags: event.target.value})
          } 
          className='input-text' 
          id="outlined-basic" 
          label="Tag" 
          variant="outlined"
          inputProps={{style:{width:"300px"}}} />
          </div>
          
          <div className="container">
          <TextField 
          value={collection.description}  
          onChange={
            (event)=> 
            updateData({description: event.target.value})
          } 
          multiline 
          rows={3} 
          className='input-text' 
          id="outlined-basic" 
          label="Description" 
          variant="outlined"
          inputProps={{style:{width:"300px"}}} />
          </div>
          
          
          <div className="bookmarked-product-list">
            <p>Select Products</p>
          </div>
          <div className="product-holder">
            {
              products.map(
                (m,i)=>{
                  const select = selected_list.includes(m.id);

                  return <ProductSelect
                          selected = {select}
                          index={i} 
                          update={updateSelectedProduct} 
                          key={i} 
                          product={m}/>})
            }
            
          </div>
        </div>
        </div>
      )
  }
  