//TODO implement edit funtion
function InputRow({title, v, number,name, onFieldUpdate, disable}) {

    const [value, setValue] = useState(v?v:"")

    let emt = true;
    if (value){
        if (value.length>0)
        emt = false;
    }
    
    const [editing, setEdit] = React.useState(false)
    const [empty, setEmpty] = React.useState(emt)

    function edit(){
        if (editing){
            setEdit(false)
            onFieldUpdate(name, value)
        }else if(empty){
            setEdit(true);
        }
        else{
            setEdit(true);
        }
    }
    function handler(event){
        setValue(event.target.value)
        onFieldUpdate(name, event.target.value)
    }

    return (
        <div className="input-row">
            <p className="input-row-name">
                {title}
            </p>

            <div className="row-input"> 
                <input maxLength={25}  type={number?"number":"text"} className={editing?"row-input-value-edit":"row-input-value"} value={value} onChange={handler}/>
                {disable?"":
                <IconButton aria-label="delete" onClick={edit}>
                    {editing?<CheckCircleIcon/>:(empty?<ControlPointIcon/>:<EditIcon />)}
                </IconButton>}
            </div>
        </div>
    )
}

