function AddressRow({title, value, field, update, number, length}){

    function handle(event){
        update(field, event.target.value)
    }

    return (
        <div className="address-row">
            <p>{title}</p>
            <input 
                onChange={handle} 
                className="address-row-input-edit" 
                maxLength={length?length:30} 
                type={number?"number":"text"} 
                value={value}/>

        </div>
    )
}
 function Address({addr, update, index, removeAddress}){
    const i = index;
    const [address, setAddress] = useState(addr);


    const [editing, setEdit] = useState(false);
    function edit(event){
        if(editing){
            setEdit(false);
            update(i,address)
        }
        else{
            setEdit(true);
        }
    }

    function updateAddress(field, value){
        const new_address = {
            type: address.type,
            line1: address.line1,
            line2: address.line2,
            district: address.district,
            pin: address.pin,
            state: address.state,
            mobile: address.mobile
        }
        
        new_address[field] = value;
        setAddress(new_address);
    }

    function onRemove(){
        removeAddress(index)
    }

    return (
        <div className="address-container">
            <div className="address-info">
                {editing?
                    <AddressRow 
                    field="type"  
                    update={updateAddress} 
                    edit={editing} 
                    title={"Title"} 
                    value={address.type}/>
                    :
                    
                    <p 
                    className="address-heading">
                        {address.type}
                    </p>
                
                }
                <IconButton aria-label="edit" onClick={edit}>
                    {editing?<CheckCircleIcon/>:<EditIcon />}
                </IconButton>

                {editing?
                    <IconButton aria-label="edit" onClick={onRemove}>
                        <DeleteIcon/>
                    </IconButton>
                    :""
                }
            </div>
            
            <AddressRow 
                field="line1"  
                update={updateAddress} 
                edit={editing} 
                title={"Line 1"} 
                value={address.line1}
                />

            <AddressRow 
                field="line2"  
                update={updateAddress} 
                edit={editing} 
                title={"Line 2"} 
                value={address.line2}
            />

            <div className="address-input-container">
                <AddressRow 
                    length={10} 
                    number 
                    field="mobile" 
                    update={updateAddress} 
                    edit={editing} 
                    title={"Mobile"} 
                    value={address.mobile}/>
                    
                <AddressRow 
                    length={6}  
                    number 
                    field="pin"  
                    update={updateAddress} 
                    edit={editing} 
                    title={"Pin"} 
                    value={address.pin}/>

                <AddressRow 
                    field="district"  
                    update={updateAddress} 
                    edit={editing} 
                    title={"District"} 
                    value={address.district}/>

                <AddressRow 
                    field="state"  
                    update={updateAddress} 
                    edit={editing} 
                    title={"State"} 
                    value={address.state}/>
            </div>
        </div>
    )
}

