import React, { useState } from 'react'
import "./style.css"
import axios from 'axios'
import { Link, useNavigate, useLocation} from "react-router-dom"
import {Appbar} from './common'
import ReactFileReader from "react-file-reader";

import TextField from '@mui/material/TextField';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputAdornment from '@mui/material/InputAdornment';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import {Features} from './homepage'
import { loadingStart,dialogMsg, loadingStop, dialogError, dialogSuccess, dialogClose} from '../dialog/dialog'

const server = process.env.REACT_APP_SERVER;


function newProduct(product, images, success){
  loadingStart();
    const link = server + "admin/product/create";
    const data = new FormData();

    data.append("token", localStorage.getItem("admin_token"))
    // console.log(product)
    data.append("product",JSON.stringify(product));
    data.append("count", images.length)

    images.forEach((img, i, arr)=>data.append("image"+i,img))

    axios.post(link, data)
    .then((res)=>{
      loadingStop()
      if (res.data!=="-1"){
        success(res.data)
        dialogSuccess("Product added successfully");
      }else{
        dialogError("Product failed to add");
      }
    });
  }



  function updateProduct(product, images){
    loadingStart()
    const link = server + "admin/product/update";
    const data = new FormData();
    data.append("token", localStorage.getItem("admin_token"))
    console.log(images)

    data.append("product",JSON.stringify(product));

    const img_map = [];

    images.forEach((img, i, arr)=>{
      if (img instanceof File){
        img_map.push("__file__");
        data.append("image"+i,img)
      }else{
        img_map.push(img);
      }
    })
    data.append("img_map", JSON.stringify(img_map))

    axios.post(link, data)
    .then((res)=>{
      loadingStop()
      console.log(res.data);
      if (res.data==="SUCCESS"){
        dialogSuccess("Updated successfully")
      }else{
        dialogError("Update Failed")
      }
    });
  }



export default function ProductUpdateView(props) {

    let model = {
        id: "",
        title: "",
        price: 0,
        tags: "",
        short: "",
        description: ""
    }
    const [new_product, setNewFlag] = useState(true);
    
    let state = useLocation().state;
    let preview_list = [];
    let img_src_list = [];

    if(new_product)
    if (state){
      console.log("STATE: ", state)
      if (state.model){
        model = state.model;
        setNewFlag(false);
        // console.log("MDOL: ", model)
        model.feature_images.forEach(m=>preview_list.push(m))
        model.feature_images.forEach(m=>img_src_list.push(m))
        
      }
    }
    const [product,setProduct] = useState(model);

    const [image_src_list, setImages] = React.useState(img_src_list);
    const [img_preview_list, setPreview] = React.useState(preview_list);
    
    function updateData(field){
      // console.log(product)
        setProduct({
            id: product.id,
            title: field.title?field.title:product.title,
            price: field.price?field.price:product.price,
            tags: field.tags?field.tags:product.tags,
            feature_images: product.feature_images,
            short: field.short?
            field.short :product.short,
            description: field.description?
            field.description :product.description,
        })
    }
    function addedSuccess(new_product){
      // console.log("NEW: ", new_product)
      setProduct(new_product)
      setNewFlag(false)
    }

    return (
    <div className="adminpanel">
        
        {new_product?
        <Appbar title="New Product" 
        back 
        nosearch 
        action={()=>newProduct(product, image_src_list, addedSuccess)}/>:
        <Appbar 
        title="Update Product" 
        back 
        nosearch 
        action={()=>updateProduct(product, image_src_list)}/>
        }

        <div className='product-update-view'>
            <Features 
            image_src_list={image_src_list} 
            img_preview_list={img_preview_list} 
            setImages={setImages} 
            setPreview={setPreview}/>
        
            <div className="container">
                <TextField 
                value={product.title} 
                onChange={
                  (event)=> 
                  updateData({title: event.target.value})
                } className='input-text' 
                id="outlined-basic" 
                label="Title" 
                variant="outlined"
                inputProps={{style:{width:"300px"}}} />
            </div>
        
            <div className="container">
                <FormControl fullWidth sx={{ m: 0 }} >
                    <InputLabel 
                      htmlFor="outlined-adornment-amount">
                        Price
                    </InputLabel>
                    <OutlinedInput
                    value={product.price}
                    onChange={(event)=> updateData({price: event.target.value})}
                    inputProps={{style:{width:"280px"}}}
                    id="outlined-adornment-amount"
                    startAdornment={<InputAdornment   position="start"></InputAdornment>}
                    label="Price"
                    />
                </FormControl>
            </div>

            <div className="container">
                <TextField 
                value={product.tags} 
                onChange={
                  (event)=> 
                  updateData({tags: event.target.value})
                }  
                className='input-text' 
                id="outlined-basic" 
                label="Tag" 
                variant="outlined"
                inputProps={{style:{width:"300px"}}} />
            </div>






            <div className="container">
                <TextField 
                value={product.short}
                onChange={
                  (event)=> 
                  updateData(
                    {short: event.target.value})
                  }
                multiline 
                rows={2}
                className='input-text' 
                id="outlined-basic" 
                label="Short Description" 
                variant="outlined"
                inputProps={{style:{width:"300px"}}} />
            </div>






            <div className="container">
                <TextField
                value={product.description}
                onChange={
                  (event)=> 
                  updateData({description: event.target.value})
                }
                multiline 
                rows={4} 
                className='input-text' 
                id="outlined-basic" 
                label="Description" 
                variant="outlined"
                inputProps={{style:{width:"300px"}}} />
            </div>
        
        </div>
    </div>
    )
  }
  