import React, { Component, useState, useEffect } from 'react'
import md5 from 'md5';
import {useNavigate} from 'react-router-dom';
import axios from 'axios'

import { loadingStart, loadingStop} from '../dialog/dialog'


const logo = 'https://ik.imagekit.io/abhilasbiswas/logo.png?updatedAt=1678945307021';

export function InputField(props) {
    return (
    <div className="container">
      <div className="input_field">
        <ion-icon name={props.icon}></ion-icon>
        <input onChange={(event)=>props.onChange(event.target.value)} type={props.password?"password":"text"} className="field" placeholder={props.title}/>
      </div>
    </div>)
}



export default function Login (props){
  const server = process.env.REACT_APP_SERVER;
  const [data, setData] = useState({
    email: "",
    password: "",
  })

  
  const navigate = useNavigate();
  
  function login(){
    // console.log("logging in...")
    try{
      loadingStart();
    axios.post(server+"admin/login",data)
    .then((response) => {
      loadingStop();
      // console.log("Login response: "+response.data);
      if (response.data!=-1){
        localStorage.setItem("admin_token", response.data);
        
        navigate("/admin/products")
      }
      else{
        alert("Please check username and passward");
      }
    })}
    catch(err){
      alert("Error in logging in!")
    }
  }

  function getAdmin() {
    // console.log("getting admin");
    const token = localStorage.getItem('admin_token');
    // console.log(token);
    if (token){
      
      axios.post(process.env.REACT_APP_SERVER+"admin/get",{token:token})
      .then((response) => {
        // console.log(response.data)
        // console.log("token response: "+ JSON.stringify(response.data));
        if(response.data!=-1){
          const admin = response.data
          // console.log("User: "+JSON.stringify(admin))
          localStorage.setItem("admin", JSON.stringify(admin))
          navigate("/admin/products")

        }else{
          localStorage.removeItem("admin");
          localStorage.removeItem("admin_token");
        }
      });
    }else{
    }
  }
  useEffect(getAdmin);


    const setEmail =(email)=>{
      data.email=email
      setData(data)
      // console.log(data)
    }
    const setPassword =(password)=>{
      data.password= md5(password);
      setData(data)
      // console.log(data)
    }

    return (
      <div className="login">
        {/* <div className="background">
            <img className="v1" src="assets/icons/background.png"/>
        </div> */}
        <div className="logo">
            <img className="logo" src={logo}/>
        </div>
        <div className="form_input">
            <InputField onChange={setEmail} icon="person-outline" title="Email"/>
            <InputField onChange={setPassword} password icon="lock-closed-outline" title="********"/>
            <input onClick={login} type="button" className="login_button" value="Log In"/>
            
        </div>
      </div>
    )
}
