import React,{useState, useEffect, useReducer} from 'react'
import {Appbar} from './common'
import Slider from 'react-slick'
import axios from 'axios'
import ReactFileReader from "react-file-reader";
import "./style.css"
import {Button} from "../common/common"
import {CollectionList} from './CollectionPanel'
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';
import { loadingStart, loadingStop, dialogError, dialogSuccess} from '../dialog/dialog'

const add_img = "https://ik.imagekit.io/abhilasbiswas/add_img.png?updatedAt=1678945306195"

const add = 'https://ik.imagekit.io/abhilasbiswas/new.png?updatedAt=1678945306764'

const server = process.env.REACT_APP_SERVER;

function FeatureCard({index, onRemove, uri}){

  const i = index;

  return (
    <div key={i}  className="edit-home-feature-carousal-card">
      <div className="edit-home-remove-collection">
        <IconButton className="remove_icon_2" aria-label="delete" size="large" onClick={
          () => {
            onRemove(i)
          }
          }>
          <ion-icon  color="danger" name="close-circle"></ion-icon>
        </IconButton>
      </div>
      <div className="edit-home-feature-container">
          <img className='edit-home-feature-image' src={uri} alt="img" />
      </div>
    </div>)
    }

//TODO Add replacement and remove option
export function Features({image_src_list, img_preview_list, setImages, setPreview}) {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
      autoplaySpeed:2000,
    };


  return (
    <div className='edit-home-feature-carousal'>
      <Slider {...settings}>
        
        {img_preview_list.map((uri,i)=>{
            return (
              <FeatureCard key={i} index={i} uri={uri} onRemove={
                (index)=>{
                  const new_image_src_list = image_src_list.filter((img,i)=>i!==index);
                  const new_img_preview_list = img_preview_list.filter((img,i)=>i!==index);
                  setImages(new_image_src_list);
                  setPreview(new_img_preview_list);
                }
              }/>
            )
        })}
        
        <div>
            
        <ReactFileReader
            fileTypes={[".png", ".jpg"]}
            base64={true}
            handleFiles={(file)=>{

            const img_src = [...image_src_list]
            img_src.push(file.fileList[0])
            setImages(img_src)
            console.log(img_src)
            const new_list = [...img_preview_list];
            new_list.push(file.base64);
            setPreview(new_list)
            }}>
            
        <div className="edit-home-feature-container">
            <img className='edit-home-feature-image' src={add_img} alt="img" />
        </div>

        </ReactFileReader>
              
        </div>
        
      </Slider>
    </div>
    );
}

function CollectionSelect({onApply}){
  const [collection, setCollection] = useState();

  return (
    <div id="edit-collection-select-container" className="edit-collection-select-close">
      <div id="select-collection-bg"/>

      <div id="select-collection-container">
        <h2>Select Collection</h2>
        <div className="colleciton-container">
          <CollectionList option onSelect={collection=>setCollection(collection)}/>
        </div>

        <div id="action">
          <Button value="Cancel" onClick={selectorClose} defocus/>
          <Button value="Apply" onClick={()=>{
            onApply(collection);
            selectorClose();
          }}/>
        </div>
      </div>
  </div>)
}

function selectorOpen(){
  const e = document.getElementById("edit-collection-select-container");
  e.className = "edit-collection-select-open";
}

function selectorClose(){
  const e = document.getElementById("edit-collection-select-container");
  e.className = "edit-collection-select-close";
}

//TODO Add remove option
function CollectionCard({m, index, onClick, onRemove}) {
  const i = index;
  return (
    <div className="groupCard edit-home-group-card">
      <div className="edit-home-remove-collection">
        <IconButton className="remove_icon" aria-label="delete" size="large" onClick={() => onRemove(i)}>
          <ion-icon  color="danger" name="close-circle"></ion-icon>
        </IconButton>
      </div>
      
      <img className='collection-icon' src={m.feature_image} onClick={() => onClick(i)}/>
      <p> {m.title} </p>
    </div>
  )
}



function reducer(state, action){
  let new_state = {index: state.index, collections: state.collections};
  switch(action.type){
    case "INITIALIZE":
      new_state.collections = action.value;
      return new_state;
    case "SET_INDEX":
      new_state.index = action.value;
      return new_state;
    case "ADD_NEW":
      // console.log("ADD NEW")
      new_state.collections.push({
        title: "Assign New",
        feature_image: "product_default.jpg"
      })
      return new_state;
    case "REMOVE":
      // console.log("INDEX: ", action.value)
      new_state.collections.splice(action.value,1);
      return new_state;
    case "APPLY":
      new_state.collections[new_state.index] = action.value;
      return new_state;
    default:
      return new_state;
  }
}


function NewCollection({onClick}){
  return (
  <div className="groupCard" onClick={onClick}>
        <img className='add-icon' src={add}/>
        <p> Add New </p>
    </div>);
}


export default function HomeManagement() {
  const [state, dispatch] = useReducer(reducer,{index: 0, collections: []})

  
  const [image_src_list, setImages] = useState([]);
  const [img_preview_list, setPreview] = useState([]);


  function updateHomePage(){
    loadingStart();
    const data = new FormData();
    console.log("IMG: ", image_src_list)
    image_src_list.forEach((img, i) => {
      data.append("feature"+i, img)
    });


    data.append("count", image_src_list.length);
    data.append("token", localStorage.getItem("admin_token"))
    
    const collection_ids = [];


    state.collections.forEach((collection) => {collection_ids.push(collection.id)})
    data.append("collections", JSON.stringify(collection_ids));
    
    
    axios.post(server+"admin/home/update", data)
    .then((response) => {
      loadingStop();
      if (response.data === "SUCCESS"){
        dialogSuccess("Updated Homepage")
      }
      else{
        dialogError("Failed to update")
      }
    })
  }


  const [home, setHome] = useState({features:[], collections:[]})

  const [updated, setUpdateFlag] = useState(false);
  useEffect(()=>{

    if (!updated) {
      setUpdateFlag(true)
      loadingStart()


      axios.post(server+"home")
      .then((response) => {
        loadingStop();
        setHome(response.data)
        console.log("COL: ", response.data)
        dispatch({type:"INITIALIZE", value: response.data.collections});
        const imgs = [];
        const img_src = []
        response.data.features.forEach(
          (image)=>{
            imgs.push(image);
            img_src.push(image);
          });
        console.log(imgs)
        setPreview(imgs)
        setImages(img_src)
      })
    }
  })

  return (
    <div className="edit-adminpanel">
        
        <Appbar 
          title="Manage Home Page" 
          back nosearch action={
            ()=>updateHomePage()
            }/>

        <Features 
          image_src_list={image_src_list} 
          img_preview_list={img_preview_list} 
          setImages={setImages} 
          setPreview={setPreview}/>

        <p className="edit-collection-title">Collections</p>

        <div className='edit-collections'>
            
          {state.collections.map((m,i)=>{
              return <CollectionCard 
              m={m} key={i} 
              index={i} 
              onClick={
                (index)=>{
                  dispatch({type:"SET_INDEX", value: index});
                  selectorOpen();
                }
              }
              onRemove = {
                (index)=>{
                  dispatch({type:"REMOVE", value: index});
                }
              }
              />
            })}
            <NewCollection onClick={
                ()=>{
                  dispatch({type:"ADD_NEW", value: null});
                }
              }/>
        </div>
        <CollectionSelect onApply={(collection)=>{
          dispatch({type:"APPLY", value:collection})
        }}/>

    </div>
  )
}
