import React,{useEffect, useState} from 'react'
import "./style.css"
import axios from 'axios'
import { Link, useNavigate} from "react-router-dom"
import {Appbar} from './common'
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';


import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';

import { loadingStart,dialogMsg, loadingStop, dialogError, dialogSuccess} from '../dialog/dialog'

const server = process.env.REACT_APP_SERVER;


const defalut_product_icon = "https://ik.imagekit.io/abhilasbiswas/default_product_icon.png?updatedAt=1678945307107"

function ProductCard({product, index, onRemove}) {
  const i = index;
    return (
        <div className="product-card">
        <Link className="product-cardx" to="/admin/product" state={{model: product}}>
            <div className="icon">
              <img src={product.feature_images[0]} alt="product" />
            </div>
            <div className="info">
              <p className='title'>{product.title}</p>
              <p className='description'>{product.short_description}</p>
            </div>
            <div className="edit">
              <p className='price'>{product.price}</p>
            </div>
            
          </Link>


            <IconButton aria-label="delete" size="large" onClick={
              () => {
                onRemove(i)
              }
              }>
              <DeleteIcon fontSize="inherit" />
            </IconButton>
        </div>)
  }


  export async function getProducts(action){
    const token = localStorage.getItem("admin_token");
    if (token){
      axios.post(server+"admin/products", {token: token})
      .then((res)=>{
          if(res.data!=-1){
            // console.log(res.data);
            action(res.data);
          }
          else{
            // console.log("Error occured")
          }
      });
    }
  }
  
  function MainView(){
    const [products, setProducts] = useState([]);
    const navigate = useNavigate();
    
    useEffect(()=>{
      if(products.length>0)
      return;
          getProducts(
            (res)=>
            {
              setProducts(res);
            })
      },[products]);


      function onRemovProduct(index){
        dialogMsg("Do you want to remove \""+ products[index].title+"\"", "Remove", remove)


        function remove(){
          loadingStart();

          const token = localStorage.getItem("admin_token");

          if (token){
              axios.post(server+"admin/product/dropout", 
              {token: token, id: products[index].id})
              .then((res)=>{
                loadingStop()
              // console.log("Collection Response: "+JSON.stringify(res.data))
              if(res.data=== "SUCCESS"){
                  const new_products = [...products];
                  new_products.splice(index, 1);
                  setProducts(new_products);
                  dialogSuccess("Collection Removed");
              }else{
                dialogError("Error in deleting")
              }
          
          });
          }
          else{
            loadingStop()
            dialogError("Some unknown error occured")
          }
        }
      }
  
    return (
      <div className="adminpanel">
        <div className='floating-action-button' id='fab'>
          <Fab onClick={()=>{
              navigate("/admin/product",{state:{new_product:true}});
            }
          } color="primary" aria-label="add" >
          <AddIcon />
          </Fab>
        </div>
          {
            products.map((m,i)=>{
              return <ProductCard key={i} index={i} onRemove={onRemovProduct} product={m}/>
            })
          }
      </div>)
  }
//TODO Add delete and update funtion
export default function ProductPanel(){
    return (
    <>
      <Appbar title="Products"/>
      <MainView/>
    </>);
    
  }