import React, {useState} from 'react'
import "./style.css"
import {useNavigate} from 'react-router-dom'

import DoneIcon from '@mui/icons-material/Done';
import IconButton from '@mui/material/IconButton';
import { purple } from '@mui/material/colors';
import {NavHead} from "../common/common";


import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import AddHomeIcon from '@mui/icons-material/AddHome';
import PermMediaIcon from '@mui/icons-material/PermMedia';
import CollectionsIcon from '@mui/icons-material/Collections';
import VisibilityIcon from '@mui/icons-material/Visibility';
import axios from 'axios'


import {Drawer} from "../common/common";

import {useAction, useSetAction } from "../../context/Context"

const server = process.env.REACT_APP_SERVER;

export function Appbar({title, back,nosearch, action, }) {
  const navigate = useNavigate();
  return (
    <div className="appbar">
        {back?
        <ion-icon onClick={()=>{navigate(-1);}} name="arrow-back-outline"/>:
        <ion-icon onClick={()=>togleNav(true)} name="menu"/> }
        {!back?<Navbar/>:""}

        <p id="admin-page-title" className="admin-page-title">{title}</p>
        
        {action?<div className="action-container">
          <IconButton onClick={action} aria-label="done">
            <DoneIcon sx={{ color: purple[50] }}  fontSize="large"/>
          </IconButton>
        </div>:""
        }

        {!nosearch?
        <div id="seach-bar-container" className='search-bar'>
          <input  placeholder='search here...' type="text" className="search-input"/>
          <ion-icon onClick={()=>{
            if (document.getElementById("seach-bar-container").className=="search-bar")
            {
              document.getElementById("admin-page-title").className = "admin-page-title2";
              document.getElementById("seach-bar-container").className = "search-bar2";
            }
            else{
              document.getElementById("admin-page-title").className = "admin-page-title";
              document.getElementById("seach-bar-container").className = "search-bar";
            }
          }} name="search"/>
        </div>
        :""}
    </div>
  )
}

let togleNav;

