function Navbar(props) {
  function login(){navigate("/admin")}

  function logout(){
    axios.post(server+"admin/logout",{token: localStorage.getItem("admin_token")})
    .then((response)=>{
      // console.log("logout response: "+response.data)
      if(response.data){
        localStorage.removeItem("admin_token");
        localStorage.removeItem("admin");
        window.location.reload();
      }
    });
  }


  let [logged_in, setLogin] = useState(false);
  const navigate = useNavigate();

  getAdmin();
  function getAdmin() {
    if (logged_in)
    return;
    
    const token = localStorage.getItem('admin_token');
    // console.log("TOKEN: ", token)

    if (token){
      axios.post(server+"admin/get",{token:token})
      .then((response) => {
        
        // console.log("token response: ", JSON.stringify(response.data));
        if(response.data!=-1){
          const admin = response.data
          // console.log("User: "+JSON.stringify(admin))
          localStorage.setItem("admin", JSON.stringify(admin))
          setLogin(true);

        }else{
          localStorage.removeItem("admin");
          localStorage.removeItem("admin_token");
          navigate("/admin")
        }
      });
    }else{
      navigate("/admin")
    }
  }

  const admin = JSON.parse(localStorage.getItem('admin'));
  
  const data = {
      name: admin?admin.name:"",
      logged_in: logged_in,
      dp: admin?admin.dp:"https://ik.imagekit.io/abhilasbiswas/default_dp.png?updatedAt=1678945306750",
      login: login,
      logout: logout
    };


  return (
    <Drawer setter={(set)=>{togleNav=set}}>
      <NavHead data={data}/>
      <List>
        <ListItem key='view' disablePadding>
          <ListItemButton onClick={() => {
            togleNav(false);
            navigate("/");
          }}>
            <ListItemIcon>
              <VisibilityIcon/>
            </ListItemIcon>
            <ListItemText primary="User View" />
          </ListItemButton>
        </ListItem>

        <ListItem key='home' disablePadding>
          <ListItemButton onClick={() => {
            togleNav(false);
            navigate("/admin/home");
          }}>
            <ListItemIcon>
              <AddHomeIcon/>
            </ListItemIcon>
            <ListItemText primary="Manage Home" />
          </ListItemButton>
        </ListItem>

        <ListItem key='collections' disablePadding>
            <ListItemButton onClick={() => {
            togleNav(false);
              navigate("/admin/collections");
            }}>
              <ListItemIcon>
                <CollectionsIcon/>
              </ListItemIcon>
              <ListItemText primary="Collections" />
            </ListItemButton>
          </ListItem>
          <ListItem key='products' disablePadding>
            <ListItemButton onClick={() => {
            togleNav(false);
              navigate("/admin/products");
            }}>
              <ListItemIcon>
                <PermMediaIcon/>
              </ListItemIcon>
              <ListItemText primary="Products" />
            </ListItemButton>
          </ListItem>
      </List>
    </Drawer>)
}

