import React,{useState, useEffect} from 'react'
import "./style.css"
import axios from 'axios'
import { Link, useNavigate} from "react-router-dom"
import {Appbar} from './common'
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';

import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';

import { loadingStart,dialogMsg, loadingStop, dialogError, dialogSuccess, dialogClose} from '../dialog/dialog'


const server = process.env.REACT_APP_SERVER;




function CollecitonCard({collection, index, onRemove}) {
    const i = index;
    return (
        <div className="product-card">
            <Link className="product-cardx" to="/admin/collection" state={{model: collection}}>
                <div className="icon">
                    <img src= {collection.feature_image} alt="collection" />
                </div>
                <div className="info">
                    <p className='title'>{collection.title}</p>
                    <p className='description'>{collection.description}</p>
                </div>
                <div className="edit">
                    <p className='price'>{collection.products.length}</p>
                </div>
                </Link>
            <IconButton aria-label="delete" size="large" onClick={
                () => {
                    onRemove(i)
                }
                }>
                <DeleteIcon fontSize="inherit" />
            </IconButton>
        </div>

    )
  }
  function CollecitonCardOption({collection,id}) {
    
    return (
            <div className="product-card" >
                <div className="icon">
                    <img src= {collection.feature_image} alt="collection" />
                </div>
                <div className="info">
                    <p className='title'>{collection.title}</p>
                    <p className='description'>{collection.description}</p>
                </div>
                <div className="edit">
                    <FormControlLabel value={id} control={<Radio />} label="" />
                    <p className='price'>{collection.count}</p>
                </div>
            </div>
    )
  }

  
async function getCollections(action){
    const token = localStorage.getItem("admin_token");
    if (token){
        axios.post(server+"admin/collections", {token: token})
        .then((res)=>{
        // console.log("Collection Response: "+JSON.stringify(res.data))
        if(res.data!=-1){
            // console.log(res.data);
            action(res.data);
        }});
    }
}

export function CollectionList({option, onSelect}){
    const [collections, setCollections] = useState([]);
    useEffect(()=>{
        if(collections.length>0)
        return;
        getCollections((res)=>
        {
            setCollections(res);
            onSelect(res[0]);
        })
      }
    );

    function onRemoveCollection(index){

        dialogMsg("Do you want to remove \""+ collections[index].title+"\"", "Remove", remove)

        function remove(){
            loadingStart();
            const token = localStorage.getItem("admin_token");

            if (token){
                axios.post(server+"admin/collection/dropout", {token: token, id: collections[index].id})
                    .then((res)=>{

                        loadingStop()
                    // console.log("Collection Response: "+JSON.stringify(res.data))
                    if(res.data=== "SUCCESS"){
                        const new_collections = [...collections];
                        new_collections.splice(index, 1);
                        setCollections(new_collections)
                        dialogSuccess("Collection Removed");
                    }else{
                        dialogError("Error in deleting")
                    }
                
                });
            }
            else{
                loadingStop()
                dialogError("Some unknown error occured")
            }
        }

        
    }

    return (
    <div>
        {option?
        
        <RadioGroup
            aria-labelledby="demo-radio-buttons-group-label"
            defaultValue={0}
            name="radio-buttons-group"
            onChange={event=>onSelect(collections[event.target.value])}
            >
        
            {collections.map((m,i)=>{ 
                return <CollecitonCardOption key={i} collection={m} id={i}/>
            })}
        </RadioGroup>
        
        :
        <>{
        collections.map((m,i)=>{ 
            return <CollecitonCard key={i} index={i} onRemove={onRemoveCollection} collection={m}/>
        })}
        </>}
    </div>)
}


