function MainView(){

    const navigate = useNavigate();

    return (
    <div className="adminpanel">
        <div className='floating-action-button' id='fab'>
            <Fab onClick={()=>{
                navigate("/admin/collection",{state:{new_collection:true}});
                }
            } color="primary" aria-label="add" >
            <AddIcon />
            </Fab>
        </div>
        <CollectionList onSelect={()=>{}}/>
    </div>)
}

export default function CollectionPanel(){

    return (
    <>
        <Appbar title="Collections"/>
        <MainView/>
    </>);
  }
  
  